import { AppBar, Box, Button, Dialog, DialogActions, DialogContent, DialogContentText, DialogTitle, FormControl, Grid, IconButton, Input, InputLabel, MenuItem, Select, SpeedDial, SpeedDialAction, SpeedDialIcon, Toolbar, Typography, styled } from "@mui/material";
import PrintIcon from '@mui/icons-material/Print';
import { useContext } from "react";
import { MainContext } from "../Contexts/MainContext";
import SaveIcon from '@mui/icons-material/Save';
import FileUploadIcon from '@mui/icons-material/FileUpload';
import { useRef } from "react";
import BuildIcon from '@mui/icons-material/Build';


export default function Header() {
    const inputRef = useRef(); // Placez useRef ici, à l'intérieur du composant

    const handleFileUploadClick = () => {
        inputRef.current.click();
    };

    const {
        handlePrint,
        selectedCodexColor,
        selectedCodex,
        cartes,
        calculateTotalValue,
        maxCarte,
        calculateCarteValue,
        handleOpenCodexSelection,
        handleCloseChange,
        handleClickOpenChange,
        openChange,
        exportAllCartes,
        handleFileChange,
        ImportButton,
        handleInputChange

    } = useContext(MainContext)
    return (

        <AppBar position='sticky' style={{ backgroundColor: selectedCodexColor, transition: 'background-color 1s ease' }} color='info' >
            <Toolbar sx={{
                justifyContent: "space-between",
                transition: "backgroundColor 1s ease",
            }}>
                <Grid container direction="row"
                    alignItems="center" spacing={2}>
                    <Grid item xs={12} md={2}>
                        <img src={process.env.PUBLIC_URL + '/logo10_weathering.png'} alt="Logo" style={{ displayPrint: 'none', height: '50px' }} />
                    </Grid>
                    <Grid item xs={12} md={2}>
                        {selectedCodex && (
                            <>
                                <Button variant="outlined" color="inherit" onClick={handleClickOpenChange}>Changer de codex</Button>
                                <Dialog open={openChange} onClose={handleCloseChange}>
                                    <DialogTitle >
                                        {"Changer de Codex"}
                                    </DialogTitle>
                                    <DialogContent>
                                        <DialogContentText >
                                            Attention, changer de codex va effacer votre liste actuelle
                                        </DialogContentText>
                                    </DialogContent>
                                    <DialogActions>
                                        <Button onClick={handleCloseChange}>Refuser</Button>
                                        <Button onClick={handleOpenCodexSelection} autoFocus>Accepter</Button>
                                    </DialogActions>
                                </Dialog>
                            </>
                        )}
                    </Grid>
                    <Grid item xs={12} md={3}>
                        <Typography variant='subtitle1'>
                            BLM : {maxCarte.formation} <b>{calculateCarteValue(maxCarte)}</b> pts
                        </Typography>
                    </Grid>
                    <Grid item xs={12} md={2}>
                        <Typography variant='subtitle1'>
                            Nombre de formations : {cartes.length}
                        </Typography>
                    </Grid>
                    <Grid item xs={6} md={2}>
                        <Typography variant='subtitle1'>
                            Points total : <b>{calculateTotalValue()}</b>
                        </Typography>
                    </Grid>
                    <Grid item xs={6} md={1}>
                        <Box alignItems={"center"} sx={{ transform: 'translateZ(0px)', flexGrow: 1 }}>
                            <SpeedDial ariaLabel="Parametres" direction="left" icon={<BuildIcon />}>
                                <SpeedDialAction key='Print' onClick={handlePrint} icon={<PrintIcon />} tooltipTitle='Imprimer' />
                                <SpeedDialAction key='Save' onClick={exportAllCartes} icon={<SaveIcon />} tooltipTitle='Sauvegarder' />
                                <SpeedDialAction key='Import' onClick={handleFileUploadClick} icon={<FileUploadIcon />} tooltipTitle='Importer' />
                            </SpeedDial>
                            <input
                                type="file"
                                ref={inputRef}
                                style={{ display: 'none' }}
                                onChange={handleFileChange} // Assurez-vous de définir la fonction handleFileChange ici
                            />
                        </Box>
                    </Grid >
                </Grid >
            </Toolbar >
        </AppBar >

    )

}